@import url('https://fonts.googleapis.com/css?family=Righteous');
.home {
  width:100vw;
  height:100vh;
}
.header {
  position: absolute;
  z-index: 2;
  margin-top:0;
  margin-bottom:0;
}

body {
  background-color: black;
}



Navigation {
  position: absolute;
}

h1 {
  font-family: 'Righteous', cursive;
letter-spacing: 2px;
color: white;
text-align: center;
font-weight: 100;
font-size: x-large;
-webkit-text-stroke: 1px black;
}






.fade-enter,
.fade-exit {
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.6s ease opacity 200ms;

}
.fade-enter,
.fade-exit-active {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  z-index: 1;
}


.slide-enter,
.slide-exit {
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.6s ease opacity 200ms;

}
.slide-enter,
.slide-exit-active {
  opacity: 0;
}
.slide-enter-active {
  opacity: 1;
  z-index: 1;
  left: 100px;
}
